<!-- 登录页 -->
<template>
	<div class="shell">
		<app-head ref="head" :head=head :stop-line-list=stopLineList :stop-line-time-today=stopLineTimeToday :current-alarm-station-num=currentAlarmStationNum></app-head>
		<div class="content" v-loading.body="loading">
			<el-carousel  :interval="3000" indicator-position="" trigger=click @change="tabChange" :autoplay="autoplay" arrow="never" ref="carousel">
				<el-carousel-item v-for="items in tabList" :key="items.name">
					<table>
						<thead>
							<tr>
								<th>报警类型-工位</th><th>异常描述</th><th>异常触发时间</th><th>异常时长</th><th>处理人</th>
							</tr>
						</thead>
						<tbody>
							<template v-if="items.tableValue.length">
								<tr v-for="item in items.tableValue">
									<td>{{item.stationText}}</td>
									<td>{{item.abnormalityDescribe}}</td>
									<td>{{item.abnormalIssuingTime}}</td>
									<td>{{item.abnormalTime}}</td>
									<td>{{item.handler}}</td>
								</tr>
							</template>
							<template v-else>
								<tr>
									<td colspan="5" class="f-tac">暂无数据</td>
								</tr>
							</template>
						</tbody>
					</table>
				</el-carousel-item>
			</el-carousel>
		</div>
		<div class="footer">
			<div class="f-df f-f1">
				<label>
					<b class="">今日质量异常数量:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}} </b>
				</label>
				<label>
					<b class="f-ml10">今日质量停线次数:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}}</b>
				</label>
				<label>
					<b class="">累计停线时长</b>
					<b class=" f-ml10">{{head.currentOutput}}</b>
				</label>
			</div>

			<div class="f-df f-f1">
				<label>
					<b class="">今日设备异常数量:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}} </b>
				</label>
				<label>
					<b class="f-ml10">今日设备停线次数:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}}</b>
				</label>
				<label>
					<b class="">累计停线时长</b>
					<b class=" f-ml10">{{head.currentOutput}}</b>
				</label>
			</div>
			
			<div class="f-df f-f1">
				<label>
					<b class="">今日物料异常数量:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}} </b>
				</label>
				<label>
					<b class="f-ml10">今日物料停线次数:{{head.dailyPlanOutput}}</b>
					<b class="f-ml10">百分比:{{head.dailyPlanOutput}}</b>
				</label>
				<label>
					<b class="">累计停线时长</b>
					<b class=" f-ml10">{{head.currentOutput}}</b>
				</label>
			</div>
		</div>
	</div>
</template>

<script>
	import appHead from "./component/head.vue";
	import jQuery from 'jquery';
	import {fetch} from '@/js/http'
	export default {
		components: {
			appHead
		},
		data() {
			return {
				loading: true,
				head:{},
				currentAlarmStationNum:"",// 当前报警工位数
				autoplay:true,
				currentPage:0,
				productionInfo:{},
				stopLineList:[],
				stopLineTimeToday:'',
				stationList: [],
				tabList:[],
				tableData: [{
					// abnormalIssuingTime: '2016-05-02',
					// alarmStation: '报警工位1',
					// abnormalTime:'异常时长',
					// abnormalityDescribe: '异常描述异常描',
					// handler:'刘德华'
				}]
			}
		},
		// 比create慢
		mounted(){
			this.fetchs().then(()=> {
				setTimeout(() => {
					this.$refs.carousel.resetItemPosition();
					this.$refs.head.$children[0].resetItemPosition();
				});
			})
		    const _this = this;
			setInterval(()=>{
				_this.fetchs()
			},30000)
		},
		computed:{
			compOutput: function() {
				var num= (parseInt(this.head.currentOutput) / parseInt(this.head.dailyPlanOutput)).toFixed(2);
				return num;
			}
		},
		methods: {
			tabChange(index){
				this.currentPage = index;
			},
			switchTab(idx){
				this.zyz = idx
				this.$refs['carousel'].setActiveItem(idx)
			},
			fetchs(){
				return fetch.post("/manage/restful/gclient/v1/ProductionPlant/getProductionPlant?version=1.0",{body :{siteCode: process.env.siteCode}})
				.then((resp) => {
					if (resp.code === "success") {
						// var index = parseInt(Math.random(0,1)*10)
						this.tabList = resp.data.abnormal.slice(0,8);

						if (resp.code === "success") {
							this.loading = false;
							this.head = resp.data.productionInformation;
							this.stopLineList = resp.data.currentAlarmStationInfo.stopLine;
							this.stopLineTimeToday = resp.data.currentAlarmStationInfo.stopLineTimeToday;
							this.currentAlarmStationNum = resp.data.currentAlarmStationInfo.currentAlarmStationNum;
							this.anormalProductionInformation = resp.data.anormalProductionInformation;
						}
					}
				});
			},
		}
	}
</script>

<style lang="less" scoped>
	

	.shell {
		font-size: 18px;
		height:100%;
		display:flex;
		flex-direction:column;
	}
	.content{
		display:flex;
		flex:1;
		flex-direction:column;
		padding:20px;
		padding-top:0;
		font-size:25px;
		.el-carousel {
			background: #eff3f4;
		}
	}

	.footer {
		height: 200px;
		display: flex;
		padding: 20px;
		flex-direction: column;
	}

	.footer label {
		flex: 1;
		text-align: center;
		display: table-cell;
		vertical-align: middle;
		line-height: 40px;
	}

	.tab-list{
		display:flex;
		height:50px;
		line-height:50px;
		div{
			flex:1;
			text-align:center;
		}
		.show{
			background:#eef1f6;
		}
	}
	.el-carousel{
		flex:1
	}
	.info {
		height: 40px;
		display: flex;
		background:#2f6298;
	}

	.info label {
		flex: 1;
		text-align: center;
		display: table-cell;
		vertical-align:middle;
		line-height:40px;
	}

	.infoItem {
		flex: 1;
		text-align: center;
		line-height:60px;
		font-size: 20px;
		color:#fb4444;
	}

	.el-carousel__item h3 {
		color: #475669;
		font-size: 18px;
		opacity: 0.75;
		/* line-height: 300px; */
		margin: 0;
	}

	.el-carousel__indicators {
		position: absolute;
		top: 0;
		height: 40px;
		font-size: 18px;
		font-weight: bolder;
	}
	
	table, tr, td{
		/* border:1px solid black; */
		text-align:center;
		font-weight: bolder;
	}

	table {
		width:92%;
		background:#eff3f4;
		margin: 0 auto;
		font-size:16px;
		font-weight:bolder;
	}
	table,
	tr,
	td {
		border: 0px
	}

	tr {
		height: 60px;
	}

	table {
		width: 100%;
		thead tr{
			border-bottom:1px solid #CCC;
			height:62px;
		}
	}
	.zyz{
		height:70px;
	}
	
</style>


