<!-- 登录页 -->
<template>
	<div id="app-head" class="head">
		<div class="info">
			<label>
				<b class="f-cw">{{head.currentDate}}</b>
			</label>
			<label>
				<b class="f-cw">日计划产量</b>
				<b class="f-cw f-ml10">{{head.dailyPlanOutput}}</b>
			</label>
			<label>
				<b class="f-cw">当前产量</b>
				<b class="f-cw f-ml10">{{head.currentOutput}}</b>
			</label>
			<label>
				<b class="f-cw">日计划完成率</b>
				<b class="f-cw f-ml10">{{(compOutput*100).toFixed(2)}}%</b>
			</label>
			<label>
				<b class="f-cw">JPH</b>
				<b class="f-cw f-ml10">{{(head.jphf*head.jphs).toFixed(2)}}</b>
			</label>
		</div>
		<div class="stationInfo f-df">
			
			<b v-if="currentAlarmStationNum" class="infoItem">当前报警工位数：{{currentAlarmStationNum}}</b>
			<el-carousel class=" f-f3" height="60px" :interval="5000">
				<template v-if="stopLineList.length">
					<el-carousel-item class="f-df" height="60px" v-for="item in stopLineList" :key="item.currentStopLineStation">
						<b class="infoItem">当前工位：{{item.currentStopLineStation}}</b>
						<b class="infoItem">预计停线时间:{{item.expectedStopTime}}</b>
						<b class="infoItem">实际停线时间:{{item.practicalStopTime}}</b>
					</el-carousel-item>
				</template>
				<template v-else>
						<el-carousel-item class="f-df" height="60px">
								<b class="infoItem">暂无数据</b>
						</el-carousel-item>
				</template>
				
			</el-carousel>
			<b class="infoItem">今日累计停线时长:{{stopLineTimeToday}}</b>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {

			}
		},

		computed:{
			compOutput: function() {
				var num= (parseInt(this.head.currentOutput) / parseInt(this.head.dailyPlanOutput)).toFixed(2);
				return num;
			}
		},
		watch: {
			stopLineList: function(val1,val2) {

			}
		},
		props: ["head","stopLineList","stopLineTimeToday","currentAlarmStationNum"]

	}

</script>

<style lang="less" scoped>

	.info {
		height: 40px;
		display: flex;
		background: #2f6298;
	}

	.info label {
		flex: 1;
		text-align: center;
		display: table-cell;
		vertical-align: middle;
		line-height: 40px;
	}

	.infoItem {
		flex: 1;
		text-align: center;
		line-height: 60px;
		font-size: 16px;
		color: #fb4444;
	}


	.el-carousel__container {
		height: 100px;
	}

	.el-carousel,
	.el-carousel__item,
	.el-carousel__container {
		height: 60px;
	}

	.el-carousel__item h3 {
		color: #475669;
		font-size: 18px;
		opacity: 0.75;
		/* line-height: 300px; */
		margin: 0;
	}




</style>


