<!-- 登录页 -->
<template>
	<div class="shell">
		<app-head :head=head :stop-line-list=stopLineList :stop-line-time-today=stopLineTimeToday></app-head>
		

		<div class="body" v-loading.body="loading">
			<table>
				<tbody>
					<tr v-for="item in compTableArray">
						<template v-for="childItem in item">
							<td v-if="childItem" :class=colorType(childItem.status)>{{childItem.stationText ? childItem.stationText.substring(0,3) : ""}}</td>
							<td v-else>--</td>
						</template>
						
					</tr>
				</tbody>
			</table>
		</div>
		<div class="bottom">
			<el-carousel height="60px" :interval="3000" trigger=click>
				<el-carousel-item v-for="(item, key) in anormalProductionInformation" :key="item.workCenterCode" height="60px">
					<b class="point">{{key+1}}、{{item.stationText}}(工位) -- {{item.andonText}}异常</b>
				</el-carousel-item>
			</el-carousel>
		</div>


	</div>
</template>

<script>
	import appHead from "./component/head.vue";
	import jQuery from 'jquery';
	import { http, fetch } from '@/js/http'

	// console.log(jQuery)
	export default {
		data() {
			return {
				loading: true,
				head: {},
				stopLineTimeToday: "",
				stopLineList: [],
				tableArray: [],
				anormalProductionInformation:[]// 底部滚动信息
			}
		},
		components: {
			appHead
		},
		computed: {
			compTableArray: function () {
				var arr = this.tableArray;
				var result = [];

				if (arguments.length > 1) {
					arr = arr.filter(function (item) {
						if (item.status == "1" && item.touchAble === true) {
							return true;
						}
					});
				}

				for (var i = 0; i < arr.length; i += 14) {
					result.push(arr.slice(i, i + 14));
				}
				
				return result;
			}
		},
		methods: {
			colorType: function(status) {
				var _class = "";
				if (status === "2") {
					_class = "orange";
				} else if (status === "3") {
					_class = "red";
				}
				return _class;
			},
			_getData() {
				
				var data = { siteCode: process.env.siteCode };
				return fetch.post("/manage/restful/sas/gclient/v1/reports/getAndonInformation?version=1.0", { body: data })
					.then((resp) => {
						if (resp.code === "success") {
							this.loading = false;
							this.head = resp.data.productionInformation;
							this.stopLineList = resp.data.stopLineList;
							this.stopLineTimeToday = resp.data.stopLineTimeToday;
							// this.tableArray = resp.data.applicationData;
							this.tableArray = resp.data.applicationData;
							if (this.tableArray.length < 140) {
								this.tableArray.length = 140;
							}	
							
							this.anormalProductionInformation = resp.data.anormalProductionInformation;
						}

					});
			},
			fullscreen(element) {
				if (element.requestFullscreen) {
					element.requestFullscreen();
				} else if (element.mozRequestFullScreen) {
					element.mozRequestFullScreen();
				} else if (element.webkitRequestFullscreen) {
					element.webkitRequestFullscreen();
				} else if (element.msRequestFullscreen) {
					element.msRequestFullscreen();
				}
			},
		},
		watch: {
			aaa: function() {

			}
		},
		mounted() {

			// this.fullscreen(document.body);
			this.loading = true;
			this._getData().then(()=> {
				setTimeout(() => {
					this.$children[1].resetItemPosition();
					this.$children[0].$children[0].resetItemPosition();
				});
			})
			

			setInterval(() => {
				this._getData();
			}, 10000);
		}
	}

</script>

<style lang="less" scoped>
	.shell {
		font-size: 16px;
	}

	.info {
		height: 40px;
		display: flex;
		background: #2f6298;
	}

	.info label {
		flex: 1;
		text-align: center;
		display: table-cell;
		vertical-align: middle;
		line-height: 40px;
	}

	.infoItem {
		flex: 1;
		text-align: center;
		line-height: 60px;
		font-size: 20px;
		color: #fb4444;
	}

	.orange {
		background: #f09b08;
	}

	.red {
		background: #fb4444;
	}

	.el-carousel__container {
		height: 100px;
	}

	.el-carousel,
	.el-carousel__item,
	.el-carousel__container {
		height: 60px;
	}

	.el-carousel__item h3 {
		color: #475669;
		font-size: 18px;
		opacity: 0.75;
		/* line-height: 300px; */
		margin: 0;
	}

	.body {
		position: absolute;
		top: 100px;
		bottom: 60px;
		width: 100%;
	}


	table,
	tr,
	td {
		border: 1px solid #eff3f4;
		text-align: center;
		font-weight: bolder;
		font-size: 23px;
	}

	table {
		height: 100%;
		width: 92%;
		background: #eff3f4;
		margin: 0 auto;
	}

	.bottom {
		position: absolute;
		bottom: 5px;
		width: 100%;
	}

	.point {
		line-height: 60px;
		display: block;
		text-align: center;
		font-size: 23px;
	}
</style>


