<template>
	<div id="nav-menu-list">
		<el-menu :unique-opened="true" default-active="2" class="el-menu-vertical-demo" theme="dark">
	        <el-submenu index="1">
				<template slot="title">我的收藏</template>
				<div v-for="(item,index) in moduleMenuList.favorMenu" :key="item.key" >
					<router-link :to="'\/'+item.url"><el-menu-item :index="index.toString()">{{item.menuText}}</el-menu-item></router-link>
				</div>
	        </el-submenu>
			<div v-for="(item,index) in moduleMenuList.navMenu" :key="item.key" >
		        <el-submenu :index="(index+1).toString()"v-if="item.children">
					<template slot="title">{{item.data.menuText}}</template>
					<div v-for="(child,i) in item.children" :key="child.key">
						<router-link :to="'\/'+child.data.url"><el-menu-item :index="index+'-'+i">{{child.data.menuText}}</el-menu-item></router-link>
					</div>
		        </el-submenu>
	        </div>
	    </el-menu>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';
	export default {
		data(){
			return {
				navMenuList:[],
			}
		},
		computed: mapGetters({
        	moduleMenuList: 'recordmoduleMenus',
  		}),
		methods:{
			zyz(){
				
			}
		}
	}
</script>

<style lang="less" scoped>
	
</style>



